import { _decorator, Component, Label, Sprite, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('DescriptionView')
export class DescriptionView extends Component {
    
    @property({type:Label})
    private levelLabel = null;

    @property({type:Sprite})
    private currentImage = null;

    setNext(level: number, sprite: SpriteFrame) {
        this.levelLabel.string = 'Level ' + level; 
        this.currentImage.spriteFrame = sprite;
    }
}
